/*  PALETTE COLORS:
 *  'Blue'    : #4285f4
 *  'Red'     : #ea4335
 *  'Yellow'  : #fbbc05
 *  'Green'   : #34a853
 */

* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
  font-family: "Lato", sans-serif;
}

section {
  padding: 60px 0;
}

p {
  line-height: 180%;
}

a {
  text-decoration: none;
}

.header__heavy {
  font-family: "Open Sans", "Lato", sans-serif;
  font-size: 64px;
  font-weight: 400;
}

.header__medium {
  font-family: "Open Sans", "Lato", sans-serif;
  font-size: 36px;
  font-weight: 400;
}

.header__light {
  font-size: 15px;
  font-weight: 100;
  text-transform: uppercase;
  letter-spacing: 3px;
  word-spacing: 8px;
}

.sectionContainer {
  margin: 0 8%;
}

.fullScreen {
  height: 100vh;
}

